body {
  /* background-color: #122216; */
  background-color: #254159;
}
.App {
  text-align: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
  display: flex;
  height: 100%;
  font-size: calc(10px + 2vmin);
}
nav {
  flex-direction: row;
  flex: 1;
}
nav ul li {
  flex-direction: row;
  list-style: none;
  display: inline;
  padding: 2%;
}
nav ul li a {
  color: #ff99c7;
  text-decoration: none;
}
.content {
  color: #dddfe2;
  flex-direction: row;
}
.title {
  color: #ffffff;
}
@media screen and (max-width: 500px) {
  .App, .lists {
    flex-direction: column;
  }
}
.headshot-container {
  display:flex;
  justify-content:center; /* horizontally center */
  align-items:center;    /* vertically center */
}
.headshot {
  display: 'flex';
  max-width: 100%;
}
.left {
  float: 'left';
  flex: 1;
}
.right {
  float: 'right';
  flex: 1;
  padding: 2.5vw;
}
.lists {
  display: flex;
  flex-direction: row;
  flex:1;
}
.list {
  flex: 1;
  width: 100%;
  text-align: left;
}
.list ul {
  list-style-type: none;
  padding-left: 0;
}
.tagline {
  font-size: calc(10px + 3vmin);
  color: #ff99c7;
}
a {
  color: #6abaff;
}
.papers-list {
  font-size: calc(10px + 1.5vmin);
}
.blog {
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.post {
  align-self: center;
  text-align: justify;
  width: 85%;
  margin-left: 10%;
  margin-right: 10%;
  border-bottom: 1px #6abaff solid;
  border-top: 1px #6abaff solid;
}